import React from 'react';
import Gallery from './data.jsx';
import User from './UserComponent.jsx';

class Page extends React.Component {

constructor(props){
        super(props);
        this.state = {
          username : '',
          useravatar:'',
          url:'',
          tag:'',
        posts:[]
         

        }
        this.handleUserName = this.handleUserName.bind(this);
this.handlePost = this.handlePost.bind(this);
  
   this.getData = this.getData.bind(this);
    
    }

    handlePost(url, tag){
     //console.log(" handlepost in merge "+url);
       this.setState({url: url,tag:tag}); 

var user = this.state.username;
       console.log(" url pop>>  "+url + " tag>> "+ tag+" name>> "+user);
 this.state.posts.push({
  im:{url},
  postbyname:"fh",
  postbyavatar:"https://pbs.twimg.com/profile_images/378800000483764274/ebce94fb34c055f3dc238627a576d251.jpeg",
  ti:"New Post",
  hashTag:{tag},
  comments:
    [
      
    ]
});


    }

  handleUserName(name) {

    this.setState({username: name}); 
   }
getData(posts){
  console.log("get data >> "+posts[0].im);

   this.setState({posts:posts}); 
}


  
render(){


 //console.log("final "+this.state.url);
return (<div>
   <User username={this.handleUserName} post = {this.handlePost}/>
 
  
    <Gallery username={this.state.username} data ={this.getData}/>
   

</div>);

}

}


export default Page;


